@value makerBlack,makerWhite, makerGreen from '../../common/colors.css';

.header {
	background-color: makerGreen;
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 0.3em;
	position: relative;
	width: 100%;
}

.logo {
	width: 3em;
}

.title {
	color: makerWhite;
	margin: 0;
}

.logout {
	position: absolute;
	right: 3em;
	top: 1em;
	padding: 0.8em;
	border-radius: 20%;
	background-color: makerBlack;
	color: makerWhite;
	outline: 0;
	border: 0;
	cursor: pointer;
}
